@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800');

@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:200,300,400,500,600,700,900');

/**************************************/

/*** Screen Sizes Mixins ***/

// 512
@mixin xs {
    @media (max-width: 511px) {
        @content;
    }
}

// 768
@mixin sm {
    @media (max-width: 800px) {
        @content;
    }
}

// 1024
@mixin md {
    @media (max-width: 1023px) {
        @content;
    }
}

// 1440
@mixin lg {
    @media (max-width: 1279px) {
        @content;
    }
}

/**************************************/

/*** Margins ***/

/* to define the sizes of wrapper */

@mixin width-sizes {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
}

/* to give 5% of margins on the side - left and right */

@mixin mg-side {
    margin-left: 5%;
    margin-right: 5%;
}

@mixin pd-side {
    padding-left: 5%;
    padding-right: 5%;
}

/* outside top and bottom margins - between modules */

@mixin mg-60 {
    margin-top: 60px;
    margin-bottom: 60px;

    @include sm {
        margin-top: 30px;
        margin-bottom: 30px;
    }

}

@mixin mg-100 {
    margin-top: 100px;
    margin-bottom: 100px;

    @include sm {
        margin-top: 50px;
        margin-bottom: 50px;
    }
}

@mixin mg-120 {
    margin-top: 120px;
    margin-bottom: 120px;

    @include sm {
        margin-top: 60px;
        margin-bottom: 60px;
    }

}

/* inside top and bottom padding - for modules with background colour */

@mixin pd-0 {
    padding-top: 0px;
    padding-bottom: 0px;
}

@mixin pd-60 {
    padding-top: 60px;
    padding-bottom: 60px;

    @include sm {
        padding-top: 30px;
        padding-bottom: 30px;
    }
}

@mixin pd-80 {
    padding-top: 80px;
    padding-bottom: 80px;

    @include sm {
        padding-top: 40px;
        padding-bottom: 40px;

    }
}

@mixin pd-100 {
    padding-top: 100px;
    padding-bottom: 100px;

    @include sm {
        padding-top: 60px;
        padding-bottom: 60px;

    }
}

@mixin pd-120 {
    padding-top: 120px;
    padding-bottom: 120px;

    @include sm {
        padding-top: 60px;
        padding-bottom: 60px;

    }
}

.t-120 {
    padding-top: 120px;

    @include sm {
        padding-top: 60px;
    }
}

.b-120 {
    padding-bottom: 120px;

    @include sm {
        padding-bottom: 60px;
    }
}

.mt-120 {
    margin-top: 120px;

    @include sm {
        margin-top: 60px;
    }
}

.mb-120 {
    margin-bottom: 120px;

    @include sm {
        margin-bottom: 60px;
    }
}

/***/

.t-100 {
    padding-top: 100px;

    @include sm {
        padding-top: 50px;
    }
}

.mt-100 {
    margin-top: 100px;

    @include sm {
