/**************************************/

/*** Colours Variables ***/

/*main*/

$m-01: #698fb7;

/*neutrals*/

$neu-01: #37383d;
$neu-02: #5c5f6c;
$neu-03: #939aa6;
$neu-04: #c2c7ce;
$neu-05: #e0e4e7;
$neu-06: #ececec;
$neu-07: #f5f5f5;
$neu-08: #ffffff;

/**************************************/

/* --------------------------*/

/* ----- MAIN ELEMENTS ----- */

/* --------------------------*/

/* // General Elements \\  */

body {
    font-family: sans-serif;
    font-family: 'Open Sans', sans-serif;
    font-weight: 200;
    margin: 0;
    padding: 0;
    font-size: 15px;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

/* /---------------------/ */

/* // Selected Elements \\  */

::-moz-selection {
    background-color: $m-01;
    color: #ffffff;
}


::selection {
    background-color: $m-01;
    color: #ffffff;
}

/* /---------------------/ */

a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

/* /---------------------/ */

p {
    color: #848484;
    font-size: 16px;
    padding: 0;
    margin: 0;
    line-height: 1.8;
    letter-spacing: 0.7;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

@media (max-width: 800px) {
    p {
        font-size: 14px;
    }
}

/* /---------------------/ */

.main-text {
    max-width: 800px;
    text-align: center;
    margin: 0 auto;
}

.main-text .left {
    text-align: left;
}


@media (max-width: 800px) {
    .main-text {
        padding: 0 10px;
    }
}


/* /---------------------/ */

#wrapper {
    width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

/* /---------------------/ */

/* // Footer \\  */

.footer {
    background-color: #313131;
    padding: 50 0;
    right: 0;
    bottom: 0;
    left: 0;
}


@media (max-width: 640px) {
    .footer {
        padding: 20 0;
    }
}


.footer p {
    display: inline;
    font-size: 12px;
    letter-spacing: 1.1;
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;
    text-align: center;
}


.footer p span {
    color: $m-01;
}


.footer a {
    color: #525252;
    font-size: 12px;
    letter-spacing: 1.6;
    font-weight: 400;
}

/* /-------------------/ */

/*  // Main Contents \\  */

.content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 0;
    margin: 0 5%;
    transition: all .3s ease-in-out;
}


.content-full {
    padding: 0 5%;

    &.blue {
        background-color: $m-01;
    }

    &.gray {
        background-color: #f0f0f0
    }

    &.dark {
        background-color: #383838;
    }
}


/* /-------------------/ */

/*  // Main Sections \\  */

.section,
.section-works,
.section-slider {
    width: 100%;
    max-width: 1440px;
    padding: 100px 0;
    transition: all .3s ease-in-out;
    margin: auto;

    @include sm {
        padding: 50px 0;
    }
}


/* /---------------------/ */

/*  // Components \\  */

button {
    color: #848484;
    display: inline-block;
    padding: 15 15 15 18;
    text-align: center;
    letter-spacing: 5px;
    background-color: #f6f6f6;
    transition: all .6s ease;
    font-size: 12px;
    cursor: pointer;
    position: relative;
    z-index: 1;
    font-family: 'Source Code Pro', monospace;
    font-weight: 500;
    border: 0;
    margin: 0 auto;
    margin-top: 40;
    display: block;

    &:after {
        content: '';
        width: 0%;
        height: 2px;
        background-color: $m-01;
        border-radius: 20px;
        position: absolute;
        margin: 0 auto;
        bottom: 0;
        left: 50%;
        transition: all 0.8s ease;

    }

    &:before {
        content: '';
        position: absolute;
        background-color: #fff;
        width: 0%;
        height: 100%;
        left: 50%;
        z-index: -1;
        top: 0;
        transition: all 0.6s ease;
    }
    &:hover {
        color: $m-01;
        text-align: center;
        z-index: 1;

        @include sm {
            color: #848484;
        }

        &:after {
            width: 30%;
            left: calc(50% - 15%);
            @include sm {
                display: none;
            }
        }

        &:before {
            width: 100%;
            left: 0%;
            @include sm {
                display: none;
            }
        }
    }

}

/*  // Links \\  */

.links {
    color: $m-01;
    transition: all .8s ease-in-out;
    position: relative;
    font-family: 'Source Code Pro', monospace;
    font-weight: 300;

    &:after {
        content: '';
        width: 0%;
        height: 2px;
        border-radius: 20px;
        background-color: $m-01;
        position: absolute;
        margin: 0 auto;
        bottom: -2px;
        left: 50%;
        transition: all 0.8s ease;
        @include sm {
            width: 0;
            text-decoration: underline;
        }
    }

    &:hover {
        &:after {
            width: 100%;
            left: 0;
            margin: 0 auto;
        }
    }
}

/*  // Back to Top \\  */

#scrollme {
    margin: 0 auto;
    height: 50px;
    cursor: pointer;
    width: 120px;

    .backtop {
        position: relative;
        color: $m-01;
        p {
            font-size: 12px;
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;
            letter-spacing: 2px;
            margin-bottom: 50px;
            opacity: 0;
            transition: all 0.6s ease;
            padding-top: 10px;
            color: $m-01;
            text-align: center;
            margin: 0 auto;
            cursor: pointer;
        }
        span {
            width: 20px;
            height: 2px;
            position: absolute;
            top: 20px;
            left: calc(50% - 10px);
            border-radius: 20px;
            transition: all 0.4s ease-in-out;
            background-color: $m-01;
        }
        span:nth-child(1) {
            transform: rotate(45deg);
            margin-left: 6px;
        }

        span:nth-child(2) {
            transform: rotate(-45deg);
            margin-left: -7px;
        }
    }

    &:hover {
        .backtop {
            p {
                opacity: 1;
                padding-top: 0px;
                font-size: 11px;
                @include sm {
                    display: none;
                }
            }

            span {
                top: 22px;
                width: 20px;
                @include sm {
                    top: 20px;
                }
            }
            span:nth-child(1) {
                left: calc(50% - 5px);
                transform: rotate(180deg);
                @include sm {
                    left: calc(50% - 10px);
                    transform: rotate(45deg);
                    width: 20px;
                    margin-left: 6px;
                }
            }
            span:nth-child(2) {
                right: calc(50% - 5px);
                transform: rotate(-180deg);
                @include sm {
                    right: calc(50% - 10px);
                    transform: rotate(-45deg);
                    width: 20px;
                    margin-left: -7px;

                }
            }
        }
    }
}



/* /---------------------/ */

/* --------------------------*/

/* --------- HEADER -------- */

/* --------------------------*/

.header {
    width: 100%;
    margin: 0 auto;
    padding: 250 0 250;
    text-align: center;
    -webkit-transition: all .6s ease-in-out;
    transition: all .6s ease-in-out;
    position: relative;
    background-color: #ccc;
    position: relative;

    @include sm {
        width: 100%;
        max-width: 100%;
        padding: 150 0 150 0;
        background-size: 250px, cover;
        background-position: center 50px;
    }

    /*  // Filter Blue \\  */
    &:before {
        background-color: $m-01;
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        z-index: 0;
        top: 0;
    }
    /*  // Header Images \\  */
    span {
        position: absolute;
        top: 0;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 25%;
        content: '';
        height: 100%;
        opacity: 0.5;
        -webkit-transition: all .6s ease-in-out;
        transition: all .6s ease-in-out;
        z-index: 1;
        -webkit-filter: grayscale(100%);
        /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);

        &:after {
            position: absolute;
            z-index: 0;
            width: 100%;
            height: 100%;
            content: '';
            left: 0;
            top: 0;
            background-color: rgba(52, 52, 52, 0.85);
            text-align: center;
        }

        &:hover {
            width: 40%;
            z-index: 2;
            opacity: 1;
            -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
            @include sm {
                width: 25%;
                -webkit-filter: grayscale(100%);
                filter: grayscale(100%);
                opacity: 0.5;
            }

            &:after {
                background-color: rgba(52, 52, 52, 0.5);
                @include sm {

                    background-color: rgba(52, 52, 52, 0.85);

                }

            }
        }
    }

    span:nth-child(1) {
        left: 0;
    }

    span:nth-child(2) {
        left: 25%;
    }

    span:nth-child(3) {
        right: 25%;
    }

    span:nth-child(4) {
        right: 0%;
    }

    /* /---------------------/ */
    /*  // Header Text \\  */
    .header-content {
        width: 100%;
        margin: 0 auto;
        padding: 0;
        z-index: 2;
        position: relative;
        top: 0;
        text-align: center;
        -webkit-transition: all .6s ease-in-out;
        transition: all .6s ease-in-out;
        h1 {
            font-size: 50px;
            color: white;
            font-weight: 100;
            width: 100%;
            position: relative;
            letter-spacing: 2px;
            margin: 0 auto;
            -webkit-transition: all .6s ease-in-out;
            transition: all .6s ease-in-out;
            @include sm {

                font-size: 35px;

            }
        }
        p {
            color: white;
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;
            position: relative;
            letter-spacing: 8;
            font-size: 14px;
            text-align: center;
            position: relative;
            z-index: 2;
            display: flex;
            justify-content: center;
            &:before {
                content: '';
                width: 160px;
                height: 6px;
                position: absolute;
                background-color: $m-01;
                z-index: -1;
                transform: translateX(-4px);
                top: 11px;
            }

        }
    }

    &:hover {
        .header-content {
            opacity: 0.2;
            @include sm {
                opacity: 1;
            }
        }
    }
}



/* --------------------------*/

/* ------- NAVIGATION ------ */

/* --------------------------*/

#nav {
    background-color: white;
    padding: 20 0;
    margin: 0;
    width: 100%;
    height: 50px;
    position: fixed;
    top: -100px;
    z-index: 3;
    -webkit-transition: all .6s ease;
    transition: all .6s ease;
    box-shadow: 0px -10px 20px black;
}


@media (max-width: 640px) {
    #nav {
        padding: 10 0;
        height: 40px;
    }
}


#nav.navshow {
    top: 0;
    z-index: 3;
}

/* /---------------------/ */

/*  // Icon Menu \\  */

#iconmenu {
    float: right;
    padding: 0;
    width: 40px;
    height: 40px;
    margin: 25px;
    position: fixed;
    z-index: 4;
    border-radius: 50px;
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
    cursor: pointer;
}


@media (max-width: 640px) {
    #iconmenu {
        margin: 10px;
    }
}


#iconmenu.iconopen {
    background-color: #f9f9f9;
}


#iconmenu.iconclose {
    background-color: white;
}


.iconopen span,
.iconclose span {
    width: 20px;
    height: 2px;
    content: '';
    position: absolute;
    left: calc(50% - 10px);
    top: 50%;
    border-radius: 20px;
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
    background-color: $m-01;
}


/*  // Icon Open \\  */

.iconopen {
    background-size: 25px 25px;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    z-index: 4;
}


.iconopen span:nth-child(1) {
    top: calc(35% - 1px);
}


.iconopen span:nth-child(2) {
    top: calc(50% - 1px);
}


.iconopen span:nth-child(3) {
    top: calc(65% - 1px);
}



/*  // Icon Close \\  */

.iconclose {
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    z-index: 5;
}

.iconclose span {
    background-color: #ccc;
}

.iconclose span:nth-child(1) {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    top: calc(50% - 1px);
}


.iconclose span:nth-child(2) {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    top: calc(50% - 1px);
    width: 0px;
    left: 50%;
    opacity: 0;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}


.iconclose span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    top: calc(50% - 1px);
}


/*  // Icon Hover \\  */

#iconmenu:hover.iconopen {
    //background-color: #e9e9e9;
}

#iconmenu:hover.iconclose {
    background-color: white;
}


.iconopen:hover span {
    opacity: 1; //background-color: #848484;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.iconopen:hover span:nth-child(1) {
    top: calc(50% - 1px);

}

.iconopen:hover span:nth-child(2) {
    transform: rotate(90deg);

}

.iconopen:hover span:nth-child(3) {
    top: calc(50% - 1px);
}


.iconclose:hover span {
    opacity: 1;
    background-color: #848484;
}


/*  // Icon - No Hover on Mobile \\  */

@media (max-width: 800px) {
    .iconclose:hover span {
        background-color: #ccc;
    }
}


@media (max-width: 800px) {
    #iconmenu.iconopen:hover {
        background-color: #f9f9f9;
    }
}


@media (max-width: 800px) {
    .iconopen:hover span {
        background-color: $m-01;
    }
}

@media (max-width: 800px) {
    .iconopen:hover span:nth-child(1) {
        top: calc(35% - 1px);
    }
}


@media (max-width: 800px) {
    .iconopen:hover span:nth-child(2) {
        transform: rotate(0deg);
    }
}


@media (max-width: 800px) {
    .iconopen:hover span:nth-child(3) {
        top: calc(65% - 1px);
    }
}


/* /---------------------/ */

/*  // Menu List \\  */

ul {
    margin: 0;
    text-align: center;
    padding: 0;

}

.menu li {
    color: #848484;
    display: inline-block;
    float: left;
    list-style: none;
    float: none;
    width: 10%;
    margin: 0 10;
    padding: 15px 0;
    letter-spacing: 4px;
    transition: all 0.4s ease;
    -webkit-transition: all .4s ease;
    position: relative;
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;
    font-size: 14px;
}


@media (max-width: 800px) {
    .menu li {
        font-size: 13px;
        margin: 0 5;
        width: 20%;
        padding: 8px 0;
        letter-spacing: 2px;
    }
}


.menu li:hover {
    color: $m-01;
    text-align: center;
}


.menu li:after {
    content: '';
    width: 0%;
    height: 2px;
    border-radius: 20px;
    background-color: $m-01;
    position: absolute;
    margin: 0 auto;
    bottom: 0;
    left: 50%;
    transition: all 0.4s ease;
    -webkit-transition: all .4s ease;
}


.menu li:hover::after {
    content: '';
    width: 30px;
    left: calc(50% - 15px);
    margin: 0 auto;
}


.menu .current {
    color: $m-01;
    text-align: center;
}


.menu .current:after {
    content: '';
    width: 30px;
    left: calc(50% - 15px);
    height: 2px;
    border-radius: 20px;
    background-color: $m-01;
    position: absolute;
    margin: 0 auto;
    bottom: 0;
    transition: all 0.9s ease;
    -webkit-transition: all .9s ease;

}

/*  // Menu - No Hover on Mobile \\  */

@media (max-width: 800px) {
    .menu li:hover {
        letter-spacing: 2px;
    }
}

@media (max-width: 800px) {
    .menu .current:after {
        width: 20px;
        left: calc(50% - 10px)
    }
}

@media (max-width: 800px) {
    .menu .current:hover::after {
        width: 20px;
        left: calc(50% - 10px)
    }
}


@media (max-width: 800px) {
    .menu li:hover::after {
        width: 0%;
    }
}



/* --------------------------*/

/* -------- CONTENT -------- */

/* --------------------------*/

/*  // Main Title \\  */

h2 {
    font-size: 35px;
    color: #848484;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    letter-spacing: 10px;
    text-align: center;
    margin: 0;
    margin-bottom: 50px;
    z-index: 2;
    text-transform: uppercase;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}


h2.white {
    color: #fff;
}


@media (max-width: 800px) {
    h2 {
        font-size: 25px;
        line-height: 35px;
    }
}


/*  // Main Title Line \\  */

h2:after {
    background-color: $m-01;
    width: 80px;
    content: '';
    height: 2px;
    border-radius: 20px;
    text-align: center;
    margin: 0 auto;
    display: block;
    margin-top: 20px;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}


h2:hover::after {
    width: 100px;
}


@media (max-width: 800px) {
    h2:after,
    h2:hover::after {
