/* ----- IMAGES WORKS ------ */

/* --------------------------*/

/*  // Image Grid \\  */

.image-container {
    display: grid;
    grid-template-columns: 50% 50%;
}


@media (max-width: 800px) {
    .image-container {
        grid-template-columns: 100%;
