@media all and (-ms-high-contrast: none),
(-ms-high-contrast: active) {
    .image-container {
        display: flex;
        flex-direction: column;
    }
}





/* /---------------------/ */

/*  // Images \\  */

.image {
    position: relative;
    width: calc(100% - 20px);
    padding-bottom: calc(100% - 20px);
    margin-bottom: 10px;
    margin-top: 10px;
    background-size: cover;
    background-position: 50%;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    z-index: 0;
    cursor: pointer;
    position: relative;
}


@media (max-width: 800px) {
    .image {
        width: 100%;
        padding-bottom: 100%;
    }
}



/*  // Image Big Horiontal \\  */

.image.big {
    padding-bottom: 50%;
}


@media (max-width: 800px) {
    .image.big {
        width: 100%;
        padding-bottom: 100%;
    }
}


/*  // Image Effects \\  */

.image:hover {
    cursor: pointer;
}


.image:before
/* filtro cinza */

    {
    content: '';
    opacity: 0.1;
    background-color: #000;
    z-index: 0;
    cursor: pointer;
    width: 100%;
    height: 100%;
    position: absolute;
}


.image:hover::before
/* filtro cinza */

    {
    width: 100%;
    height: 100%;
    opacity: 1;
    background-color: transparent;
}



.image:after
/* filtro azul */

    {
    position: absolute;
    content: '';
    opacity: 0;
    background-color: $m-01;
    transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    cursor: pointer;
    width: calc(100% - 60px);
    left: 30px;
    height: calc(100% - 60px);
    top: 30px;
    z-index: 1;

}

.image:hover::after
/* filtro azul */

    {
    content: '';
    opacity: 0.9;
    background-color: $m-01;
    color: white;
    z-index: 1;
    cursor: pointer;
    width: 100%;
    left: 0%;
    height: 100%;
    top: 0%;
}


/* Images - No hover on mobile */

@media (max-width: 800px) {
    .image:hover::after {
        display: none;
        opacity: 0;
    }
}

@media (max-width: 800px) {
    .image:hover::before/* filtro cinza */
        {
        opacity: 0.1;
        background-color: #000;
    }
}


/* /---------------------/ */

/*  // On Going Works \\  */

.image.ongoing {
    padding-bottom: 40%;
    background-color: white;
}


.image.ongoing:before
/* filtro azul - default */

    {
    position: absolute;
    width: 100%;
    height: 100%;
    content: '';
    opacity: 0.7;
    background-color: $m-01;
    transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    cursor: pointer;
    left: 0%;
    top: 0%;
    z-index: 1;
}


.image.ongoing:after
/* filtro azul - hover*/

    {
    position: absolute;
    content: '';
    opacity: 0;
    background-color: $m-01;
    transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    cursor: pointer;
    width: calc(100% - 60px);
    left: 30px;
    height: calc(100% - 60px);
    top: 30px;
}


.image.ongoing:hover::after
/* filtro azul - hover */

    {
    opacity: 0.9;
    color: white;
    z-index: 1;
    cursor: pointer;
    width: calc(100%);
    left: 0px;
    height: calc(100%);
    top: 0px;
}


.image.ongoing span {
    color: white;
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;
    margin: 0 auto;
    padding: 0;
    letter-spacing: 8;
    font-size: 14px;
    text-align: center;
    position: absolute;
    z-index: 2;
    width: 100%;
    top: 45%;
    transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
}


.image.ongoing:hover span {
    opacity: 0;
    top: 35%;
}


.image.ongoing span:before {
    content: '';
    width: 220px;
    height: 6px;
    position: absolute;
    background-color: $m-01;
    z-index: -1;
    transform: translateX(48%);
    right: 50%;
    top: 7px;
    transition: all .6s ease-in-out;
    -webkit-transition: all .6s ease-in-out;
}


.image.ongoing:hover span:before {
    width: 0;

}

/* On going works - No hover on mobile */

@media (max-width: 800px) {
    .image.ongoing:hover::after {
        display: none;
        opacity: 0
    }
}


@media (max-width: 800px) {
    .image.ongoing:hover span {
        width: 100%;
        top: 45%;
        opacity: 1;
    }
}


@media (max-width: 800px) {
    .image.ongoing span:hover {
        width: 100%;
        top: 45%;
    }
}


@media (max-width: 800px) {
    .image.ongoing:hover span:before {
        width: 220px;
        opacity: 1;
        top: 45%;
    }
}



