/* --------------------------*/

/* ----- ICON SECTION ------ */

/* --------------------------*/

/*  // Icon Section \\  */

.row-icons {
    display: grid;
    grid-template-columns: 16% 16% 16% 16% 16% 16%;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;

    @include md {
        grid-template-columns: 33% 33% 33%;

    }
    @include sm {
        grid-template-columns: 50% 50%;
    }

    .icon-section {
        width: 120px;
        text-align: center;
        margin: 0 auto;
        position: relative;
        z-index: 1;

        &:before {
            background-color: white;
            position: absolute;
            content: '';
