            z-index: 1;
            width: auto;
            height: auto;
            @include sm {
                background-color: $m-01;
            }
        }

        &:after {
            background-color: $m-01;
            position: absolute;
            content: '';
            transition: all 0.4s ease-in-out;
            border-radius: 1px;
            z-index: 2;
        }

        /*  // Content Icons \\  */
        p {
            font-size: 12px;
            line-height: 20px;
            margin: 15 0 0 0;
