                color: $m-01;
            }
        }


        @media (max-width: 800px) {
            .icon-section p,
            .icon-section:hover p {
                font-size: 11px;
                line-height: 18px;
                margin: 10 0 0 0;
                color: #848484;
            }
        }
    }

    /* icon 1 */
    .icon-section:nth-child(1) {
        &:before {
            width: 13px;
            height: 12px;
            top: 70px;
            left: 37px;
        }

        &:after {
            width: 0px;
            height: 12px;
            top: 70px;
            left: 37px;
        }

        &:hover {
            &:after {
                width: 13px;
            }
        }

    }

    /* icon 2 */
    .icon-section:nth-child(2) {
        &:before {
            width: 52px;
            height: 9px;
            top: 87px;
            left: 25px;
        }
        &:after {
            width: 0px;
            height: 9px;
            top: 87px;
            left: 25px;
        }
        &:hover {
            &:after {
                width: 52px;
            }
        }
    }

    /* icon 3 */
    .icon-section:nth-child(3) {
        &:before {
            width: 17px;
            height: 27px;
            top: 58px;
            left: 66px;
        }

        &:after {
            width: 17px;
            height: 0px;
            top: 58px;
            left: 66px;
        }

        &:hover {
            &:after {
                height: 27px;
            }
        }
    }

    /* icon 4 */
    .icon-section:nth-child(4) {
        &:before {
            width: 17px;
            height: 17px;
            top: 71px;
            left: 49px;
            z-index: -2;
        }

        &:after {
            width: 17px;
            height: 0px;
            top: 71px;
            left: 49px;
            z-index: -1;
        }

        &:hover {
            &:after {
                height: 17px;
            }
        }
    }

    /* icon 5 */
    .icon-section:nth-child(5) {
        &:before {
            width: 21px;
            height: 19px;
            top: 88px;
            left: 22px;
        }

        &:after {
            width: 0px;
            height: 19px;
            top: 88px;
            left: 22px;
        }


        &:hover {
            &:after {
                width: 21px;
            }
        }
    }

    /* icon 6 */
    .icon-section:nth-child(6) {
        &:before {
            width: 12px;
            height: 12px;
            top: 81px;
            left: 62px;
            border-radius: 50px;
        }

        &:after {
            width: 0px;
            height: 0px;
            top: 87px;
            left: 68px;
            border-radius: 50px;
        }

        &:hover {
