                height: 12px;
                top: 81px;
                left: 62px;
            }
        }
    }

}

/* --------------------------*/

/* ---- SOCIAL SECTION ----- */

/* --------------------------*/

/*  // Contacts \\  */

.contacts {
    display: flex;
    justify-content: space-around;
    text-align: center;
    transition: all 0.8s ease;
    -webkit-transition: all .8s ease;

    /* social item*/
    .social {
        background-color: #4a4a4a;
        padding: 100 0;
        z-index: 2;
        Width: 100%;
        position: relative;
        transition: all 0.6s ease;
        -webkit-transition: all .3s ease;
        display: flex;
        align-items: center;
        justify-content: center;


        /*  // Social Texts \\  */
        .social-title {
            color: #ccc;
            letter-spacing: 10px;
            font-size: 14px;
            position: absolute;
            letter-spacing: 4px;
            width: 100%;
            transition: all 0.9s ease;
            top: 35%;
            opacity: 0;
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;

            @include sm {
                top: 70%;
                opacity: 1;
                letter-spacing: 2;
                font-size: 11px;
                color: #999;
            }
        }

        .social-link {
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;
            font-size: 12px;
            opacity: 0;
            color: white;
            transition: all 0.7s ease;
            position: absolute;
            width: 100%;
            top: 60%;

            @include sm {
                display: none;
            }
        }
        /*  // Icon Social \\  */
        span {
            width: 60px;
            height: 60px;
            content: '';
            position: absolute;
            opacity: 1;
            transition: all 0.4s ease;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;

            @include sm {
                width: 40px;
                height: 40px;
                left: calc(50% - 20px);
                top: 20%
            }
        }

        /*  // Background Icons \\  */
        &:nth-child(1) {
            span {
                background-image: url("../images/icons/icons_mail.svg");
            }
        }
        &:nth-child(2) {
            background-color: #525252;
            span {
                background-image: url("../images/icons/icons_behance.svg");
            }
        }
        &:nth-child(3) {
            span {
                background-image: url("../images/icons/icons_linkedin.svg");
            }
        }

        &:before {
            content: '';
            position: absolute;
            background-color: #313131;
            width: 0%;
            height: 100%;
            z-index: -1;
            top: 0;
            transition: all 0.7s ease;
            color: #ccc;
        }
        &:after {
            content: '';
            width: 0%;
            height: 2px;
            border-radius: 20px;
            background-color: $m-01;
            position: absolute;
            bottom: 0;
            transition: all 0.9s ease;
        }

        @include sm {
            padding: 50 0;
        }

        &:hover {
            color: $m-01;
            letter-spacing: 6px;
            text-align: center;
            &:before {
                width: 100%;
                @include sm {
                    width: 0;
                }
            }
            &:after {
                width: 100%;
                @include sm {
                    width: 0;
                }
            }
            .social-title {
                opacity: 1;
                top: 40%;
                letter-spacing: 6px;
                @include sm {
                    top: 70%;
                    opacity: 1;
                    letter-spacing: 2;
                    font-size: 11px;
                    color: #999;
                }
            }

            .social-link {
                opacity: 0.2;
                letter-spacing: 1;
                top: 55%;
                @include sm {
                    display: none;
                }
            }

            span {
                width: 50%;
                height: 50%;
                opacity: 0.03;
                @include sm {
                    width: 40px;
                    height: 40px;
                    left: calc(50% - 20px);
                    top: 20%;
                    opacity: 1;

                }
            }
        }
    }
}

