/* --------------------------*/

/* ------- SLICK CODE ------ */

/* --------------------------*/

/*  // Arrows \\  */

.slick-prev,
.slick-next {}

.slick-prev:after,
.slick-next:after {
    display: none;
    opacity: 0;
}


@media (max-width: 1023px) {
    .slick-prev,
    .slick-next {
        display: none;
    }
}


/*  // Prev \\  */

.slick-prev,
.slick-next {
    width: 30px;
    height: 30px;
    top: calc(50% - 15px);
    opacity: 1;
    -webkit-transition: all .5s ease;
    transition: all .5 ease;
    background-color: transparent;
    margin: 0px;
    padding: 0px\;
}


.slick-prev:before,
.slick-next:before {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 0px;
    content: '';
    left: 0;
    background-size: cover;
    background-color: transparent;
}

.slick-prev:before {
    background-image: url(../images/icons/icon_left_c.png);

}

.slick-next:before {
    background-image: url(../images/icons/icon_right_c.png);

}

.slick-prev.slick-disabled:before,
.slick-prev.slick-disabled:hover:before,
.slick-next.slick-disabled:hover:before,
.slick-next.slick-disabled:before {
    opacity: 0.2;
    left: 0;
}





/*  // Hovers Prev \\  */

.slick-prev:hover:before {
    left: -10px;

}



/*  // Next \\  */

/*  // Hovers Next \\  */

.slick-next:hover:before {
    left: 10px;

}






.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
    opacity: 1;
    background-color: #eeeeee;

}



